import React from "react";
import './Choice.css';

function Choice() {

    return (
        <section className="choice">
            <div className="choice__block">
            <h2 className="education__title"> &#160;<span className="education__title-span"></span>!</h2>
            <div className="choice__content">
                <div className="choice__content-text">
                    <p className="school__subtitle">          ,  ,
                             ,      .</p>
                </div>
                <form className="choice__form">
                    <div className="choice__container">
                        <input className="choice__input" placeholder="" type="number" />
                        <label className="choice__label">      
                            <span className="choice__number"> +375(33) 637-30-79</span></label>
                    </div>
                    <div className="choice__container">
                        <button className="choice__button"></button>
                        <label className="choice__button-label">  ,      .</label>
                    </div>
                </form>
            </div>
            </div>
        </section>
    )
}
export default Choice;